.main-div-container {
  display: flex;
  flex-wrap: wrap;
  gap: 25px;
  margin-left: 25px;
}
.big-container {
  background-color: rgb(194, 224, 245);
  border-radius: 50px;
  padding: 10px;
}
.user-info {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  gap: 10px;
  width: 400px;
}
.user-username {
  text-align: center;
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 20px;
}
p, h3 {
  border-radius: 10px;
  padding: 2px 5px;
}